import React, { Component } from 'react';
import moment from 'moment';
import { requestHeaderDELETEWithAuth, requestHeaderGETWithAuth, requestHeaderPOSTWithAuth } from 'src/Rest-API/Header';
import { getUserDataFromLocalStorage, logoutProcedureForLocalStorage } from 'src/Utils/AuthRelatedThings';
import { Logger, getSearchResult, openPageInNewTab, reloadPage, goHome, filterById } from '../../Utils/Utils';
import { Loading } from '../Statuses/Statuses';
import Table from 'react-bootstrap/Table';
import {
    CButton,
    CCard,
    CCardBody,
    CCardGroup,
    CCol,
    CContainer,
    CForm,
    CInput,
    CInputGroup,
    CInputGroupPrepend,
    CInputGroupText,
    CRow,
    CTextarea
  } from '@coreui/react';
import CIcon from '@coreui/icons-react';
import StatusModal from '../Modals/StatusModal';
import FailureModal from '../Modals/FailureModal';
import { brandSet } from '@coreui/icons';
import MaterialReactTable from 'material-react-table';
import Select from 'react-select';
import { yesAndNoList } from 'src/Utils/Enums';
import { BACKEND_PRICE_TRACKER_SERVICE_BASE_URL } from 'src/Rest-API/EnvironmentSpecificThings';

//default
const FILE_NAME = "Store.js";

//default
const initialState = {
    isLoaded: false,
    loadingModalVisible: false,
    loadingModalState: 0,
    failureModalVisible: false,
    storeListData: [],
    locationListData: [],
    locationListDataSelectTagSpecific: [],
    newStoreData: {
        isCloudStore: null,
        locationId: null
    }
};

const tableColumnData = [
    {
        accessorKey: 'id',
        header: 'ID',
        enableEditing: false,
    },
    {
        accessorKey: 'name',
        header: 'name',
        enableEditing: true,
    },
    {
        accessorKey: 'type',
        header: 'type',
        enableEditing: true,
        Cell: ({ renderedCellValue, row }) => (renderedCellValue === null) ? "N/A" : renderedCellValue
    },
    {
        accessorKey: 'isCloudStore',
        header: 'isCloudStore',
        enableEditing: true,
        Cell: ({ renderedCellValue, row }) => (renderedCellValue ? "YES" : "NO")
    },

    {
        accessorKey: 'location',
        header: 'location',
        enableEditing: true,
        Cell: ({ renderedCellValue, row }) => (renderedCellValue === null) ? "N/A" : (renderedCellValue.name + " [" + renderedCellValue.description + "][" + renderedCellValue.id + "]")
    },
    
    {
        accessorKey: 'createdAt',
        header: 'Created At',
        enableEditing: false,
        Cell: ({ renderedCellValue, row }) => (moment(renderedCellValue).zone('IST').format('YYYY/MM/DD HH:mm:ss ddd'))
    },
    {
        accessorKey: 'updatedAt',
        header: 'Updated At',
        enableEditing: false,
        Cell: ({ renderedCellValue, row }) => (moment(renderedCellValue).zone('IST').format('YYYY/MM/DD HH:mm:ss ddd'))
    },
];


class Store extends Component {

    //default
    constructor(props) {
        super(props);
        this.state = JSON.parse(JSON.stringify(initialState));
    }

    //default
    resetStateToDefault() {
        this.setState(JSON.parse(JSON.stringify(initialState)));
    }

    //default
    reloadData() {
        this.resetStateToDefault();
        this.fetchLocationDataList();
    }

    //default
    componentDidMount() {
        document.title = "Expenses - Store";
        this.fetchLocationDataList();
    }

    fetchLocationDataList() {
        let API_URL = BACKEND_PRICE_TRACKER_SERVICE_BASE_URL + '/location/list?offset=0&from=admin-portal';
        const userData = getUserDataFromLocalStorage();

        fetch(API_URL, requestHeaderGETWithAuth(userData.authToken))
        .then(response => {
            if(response.status === 200)
                return response.json();
            if(response.status === 401) {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
                setTimeout(() => {
                    logoutProcedureForLocalStorage();
                    reloadPage();
                }, 500);
            } else {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
            throw new Error("HTTP status is " + response.status);
        })
        .then(
            (result) => {
                Logger(FILE_NAME, "fetchLocationDataList", "FETCH_1", "result", result);
                if(result.code === 1001) {
                    //success
                    const locationListDataSelectTagSpecific = result.data.map((element) => {
                        return {
                            value: element.id,
                            label: element.name + " [**" +  element.description + "]"
                        };
                    });
                    this.setState({
                        locationListData: result.data,
                        locationListDataSelectTagSpecific
                    });
                    this.fetchStoreDataList();
                } else {
                    //something horrible happened
                    this.setState({
                        loadingModalVisible: false,
                        loadingModalState: 0,
                        failureModalVisible: true
                    });
                }
            },
            (error) => {
                Logger(FILE_NAME, "fetchLocationDataList", "FETCH_1", "error", error);
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
        );
    }

    fetchStoreDataList() {
        let API_URL = BACKEND_PRICE_TRACKER_SERVICE_BASE_URL + '/store/list?offset=0&from=admin-portal';
        const userData = getUserDataFromLocalStorage();

        fetch(API_URL, requestHeaderGETWithAuth(userData.authToken))
        .then(response => {
            if(response.status === 200)
                return response.json();
            if(response.status === 401) {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
                setTimeout(() => {
                    logoutProcedureForLocalStorage();
                    reloadPage();
                }, 500);
            } else {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
            throw new Error("HTTP status is " + response.status);
        })
        .then(
            (result) => {
                Logger(FILE_NAME, "fetchStoreDataList", "FETCH_1", "result", result);
                if(result.code === 1001) {
                    const storeListData = result.data.map(store => {
                        store.location = filterById(this.state.locationListData, store.locationId);
                        return store;
                    });
                    //success
                    this.setState({
                        isLoaded: true,
                        storeListData
                    });
                } else {
                    //something horrible happened
                    this.setState({
                        loadingModalVisible: false,
                        loadingModalState: 0,
                        failureModalVisible: true
                    });
                }
            },
            (error) => {
                Logger(FILE_NAME, "fetchStoreDataList", "FETCH_1", "error", error);
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
        );
    }

    submitCreateStoreForm = (event) => {
        event.preventDefault();

        let reqData = {
            name: event.target.inputName.value,
            type: event.target.inputType.value,
        };

        const validationResult = this.doesStateContainAllRequiredStoreParams();
        if(validationResult !== null) {
            alert(validationResult + " . ‚ÑπÔ∏è Form validation failed.");
            return;
        }
        
        reqData.isCloudStore = this.state.newStoreData.isCloudStore === 'YES' ? true : false;
        reqData.locationId = this.state.newStoreData.locationId;

        this.setState({
            loadingModalVisible: true,
            loadingModalState: 0
        });

        this.callCreateStoreApi(reqData);
    }

    doesStateContainAllRequiredStoreParams() {
        if(this.state.newStoreData.isCloudStore === null)
            return "isCloudStore NOT selected";
        if(this.state.newStoreData.isCloudStore === 'NO' && this.state.newStoreData.locationId === null)
            return "location is MANTORY for non-cloud stores";
        
        return null;
    }

    callCreateStoreApi(newStore) {
        let API_URL = BACKEND_PRICE_TRACKER_SERVICE_BASE_URL + '/store/save?offset=0&from=admin-portal';
        const userData = getUserDataFromLocalStorage();
        fetch(API_URL, requestHeaderPOSTWithAuth(JSON.stringify(newStore), userData.authToken))
        .then(response => {
            if(response.status === 200)
                return response.json();
            if(response.status === 401) {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
                setTimeout(() => {
                    logoutProcedureForLocalStorage();
                    reloadPage();
                }, 500);
            } else {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
            throw new Error("HTTP status is " + response.status);
        })
        .then(
            (result) => {
                Logger(FILE_NAME, "callCreateStoreApi", "FETCH_1", "result", result);
                if(result.code === 1001) {
                    //success
                    this.setState({
                        loadingModalState: 1,
                    });
                    setTimeout(() => {this.reloadData();}, 200);
                } else {
                    //something horrible happened
                    this.setState({
                        loadingModalVisible: false,
                        loadingModalState: 0,
                        failureModalVisible: true
                    });
                }
            },
            (error) => {
                Logger(FILE_NAME, "callCreateStoreApi", "FETCH_1", "error", error);
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
        );
    }


    //default
    render() {
        //default
        Logger(FILE_NAME, "render", "", "this.state", this.state);

        return (
            this.state.isLoaded ? 
            <div>
                <h1>STORE &nbsp;&nbsp;&nbsp;&nbsp;&nbsp; 
                    <CButton color="success" className="px-4" onClick={() => this.reloadData()}>Reload Data</CButton>
                </h1>
                <br />

                {
                    this.state.isLoaded ?
                        <MaterialReactTable
                            initialState={{ density: 'compact' }}
                            columns={tableColumnData} 
                            data={this.state.storeListData}
                            enablePagination = {true}

                            editingMode={"row"}
                            enableEditing
                            onEditingRowSave={(exitEditingMode) => this.handleSaveRowEdits(exitEditingMode)}
                        /> : <br/>
                }
                
                <br /><br />

                {
                    /* Create new store */
                    <CContainer>
                    <CRow className="justify-content-center">
                    <CCol md="8">
                        <CCardGroup>
                        <CCard className="p-4">
                            <CCardBody>
                            <CForm onSubmit={this.submitCreateStoreForm}>
                                <h1>Save a new Store</h1>
                                <CInputGroup className="mb-3">
                                    <CInputGroupPrepend>
                                        <CInputGroupText>
                                        <CIcon name="cilPencil" />
                                        </CInputGroupText>
                                    </CInputGroupPrepend>
                                    <CInput
                                        placeholder="Store Name" 
                                        autoComplete="on"
                                        type="text"
                                        step="any"
                                        id="inputName"
                                        label="Store Name"
                                        required
                                    />
                                </CInputGroup>

                                <CInputGroup className="mb-3">
                                    <CInputGroupPrepend>
                                        <CInputGroupText>
                                        <CIcon name="cilPencil" />
                                        </CInputGroupText>
                                    </CInputGroupPrepend>
                                    <CInput
                                        placeholder="Store Type" 
                                        autoComplete="on"
                                        type="text"
                                        step="any"
                                        id="inputType"
                                        label="Store Type"
                                        required
                                    />
                                </CInputGroup>

                                <CIcon name="cilPencil" /> <b>Is Cloud Store ? :</b>
                                <br />
                                <Select 
                                    onChange={(choice) => {
                                        const newStoreData = this.state.newStoreData;
                                        newStoreData.isCloudStore = (choice === null) ? null : choice.value;
                                        this.setState({
                                            newStoreData
                                        });
                                    }}
                                    options={yesAndNoList}
                                    isClearable={true}
                                />
                                <br />

                                <CIcon name="cilPencil" />&nbsp;&nbsp; <b>Location :</b> {(this.state.newStoreData.isCloudStore === 'YES') ? "‚õîÔ∏è ‚ÑπÔ∏è Not reqd. for online store" : "" }
                                <br />
                                <Select 
                                    onChange={(choice) => {
                                        const newStoreData = this.state.newStoreData;
                                        newStoreData.locationId = (choice === null) ? null : choice.value;
                                        this.setState({
                                            newStoreData
                                        });
                                    }}
                                    options={this.state.locationListDataSelectTagSpecific}
                                    isClearable={true}
                                />
                                <br />

                                <CRow>
                                    <CCol xs="6">
                                        <CButton color="success" className="px-4" type='submit'>SAVE</CButton>
                                    </CCol>
                                    <CCol xs="6" className="text-right">
                                        <CButton color="danger" className="px-0" onClick={() => this.reloadData()}>&nbsp;Reset values&nbsp;</CButton>
                                    </CCol>
                                </CRow>
                            </CForm>
                            </CCardBody>
                        </CCard>
                        </CCardGroup>
                    </CCol>
                    </CRow>
                    </CContainer>  
                }

                {/* //default */}
                <StatusModal visible={this.state.loadingModalVisible} loadingModalState={this.state.loadingModalState} />
                <FailureModal visible={this.state.failureModalVisible} />
            </div>
            : <Loading />
        );
    }
}

export default Store;
